.contact {
  background-color: #2c2c2c;
  border-radius: 15px;

  @include tablet {
    padding: 0 21;
  }

  @include design {
    height: 1000px;

    --columns: 12;

    display: grid;
    grid-template-columns: repeat(var(--columns), 1fr);
    gap: 30px;
  }

  @include desktop {
    height: 1000px;

    --columns: 12;

    display: grid;
    grid-template-columns: repeat(var(--columns), 1fr);
    gap: 30px;
  }

  @include large-screen {
    padding: 0 500px 80 607;
  }

  &__form {
    margin: 0;
    padding: 0 20;
    display: flex;
    flex-direction: column;
    gap: 40px;

    @include tablet {
      width: 362px;
    }

    @include design {
      display: grid;
      grid-column: 1 / 4;
      padding: 56 206;
    }

    @include desktop {
      display: grid;
      grid-column: 1 / 4;
      padding: 56 206;
    }

    &__email {
      font-family: $font-family;
      font-size: 15px;
      line-height: 22.5px;
      font-weight: 600;
      height: 41px;
      background-color: #2c2c2c;
      border-style: none;
      transition: all 0.3s ease-in-out;
      border-bottom: 1px solid #fff;
      color: #fff;

      @include tablet {
        // width: 360;
      }

      &:focus {
        outline: none;
        border-bottom: 1px solid #144ed4;
      }
    }

    &__name {
      font-family: $font-family;
      font-size: 15px;
      line-height: 22.5px;
      font-weight: 600;
      height: 41px;
      background-color: #2c2c2c;
      border-style: none;
      border-bottom: 1px solid #fff;
      color: #fff;
      transition: all 0.3s ease-in-out;

      &:focus {
        outline: none;
        border-bottom: 1px solid #144ed4;
      }
    }

    &__message {
      font-family: $font-family;
      font-size: 15px;
      line-height: 22.5px;
      font-weight: 600;
      height: 41px;
      background-color: #2c2c2c;
      border-style: none;
      border-bottom: 1px solid #fff;
      color: #fff;
      transition: all 0.3s ease-in-out;

      &:focus {
        outline: none;
        border-bottom: 1px solid #144ed4;
      }
    }

    &__button {
      height: 56px;
      background-color: #144ed4;
      border-style: none;
      border-radius: 8px;
      color: #fff;
      font-family: $font-family;
      font-size: 15px;
      line-height: 22.5px;
      font-weight: 600;
      margin-top: 16px;

      @include tablet {
        width: 264;
      }
    }
  }

  &__send {
    &__title {
      padding: 72 20px 48;
      font-size: 32px;
      font-weight: 600;
      color: #fff;
      line-height: 41.6px;

      @include design {
        // display: grid;
        // grid-column: 1 / 4;
        padding: 180 0 0 206;
      }

      @include desktop {
        // display: grid;
        // grid-column: 1 / 4;
        padding: 180 0 0 206;
      }
    }
  }

  &__us {
    padding: 80 20;
    display: flex;
    flex-direction: column;
    gap: 40px;

    @include design {
      height: auto;
      width: 362;
      gap: 48px;
      display: grid;
      grid-column: 4 / 8;
      padding: 180 20 164;
    }

    @include desktop {
      height: auto;
      width: 362;
      gap: 48px;
      display: grid;
      grid-column: 4 / 8;
      padding: 180 20 164;
    }

    &__title {
      font-family: $font-family;
      font-weight: 600;
      font-size: 32px;
      line-height: 48px;
      color: #fff;

      @include design {
        height: 40px;
      }
    }

    &__phone {
      font-family: 'Open Sans', sans-serif;
      font-size: 24px;
      line-height: 36px;
      font-weight: 600;
      text-decoration: none;
      color: #fff;

      @include design {
        height: 70px;
      }

      &__title {
        font-family: $font-family;
        font-weight: 700;
        font-size: 13px;
        line-height: 19.5px;
        color: #fff;
        letter-spacing: 3px;
        opacity: 50%;
      }
    }

    &__address {
      font-family: 'Open Sans', sans-serif;
      font-size: 24px;
      line-height: 36px;
      font-weight: 600;
      color: #fff;

      @include design {
        height: 100px;
      }

      &__title {
        font-family: $font-family;
        font-weight: 700;
        font-size: 13px;
        line-height: 19.5px;
        color: #fff;
        letter-spacing: 3px;
        opacity: 50%;
      }

      &__map {
        text-decoration: none;
        color: #fff;
      }
    }

    &__socialas {
      &__title {
        font-family: $font-family;
        line-height: 19.5px;
        letter-spacing: 3px;
        opacity: 50%;
        color: #fff;
      }

      &__icons {
        display: flex;
        flex-direction: row;
        gap: 32px;
        align-items: center;
      }
    }
  }

  &__footer {
    padding: 0 20;

    @include design {
      height: 0;
      padding: 10 10 10 210;
      grid-column: 1 / 10;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
    }

    @include desktop {
      height: 0;
      padding: 10 10 10 210;
      grid-column: 1 / 10;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
    }

    &__menu {
      display: flex;
      flex-direction: column;
      gap: 24px;
      font-family: $font-family;
      font-size: 13px;
      line-height: 19.5px;
      color: #fff;
      font-weight: 700;
      letter-spacing: 3px;
      padding: 48px 0 80;

      @include tablet {
        display: flex;
        flex-direction: row;
        gap: 40px;
      }

      @include design {
        padding: 0;
        gap: 64px;
      }

      @include desktop {
        padding: 0 54;
        gap: 64px;
      }
    }
  }
}
