.services {
  --columns: 2;

  display: grid;
  row-gap: 24px;
  grid-template-columns: repeat(var(--columns), 1fr);
  padding: 0 18;

  @include tablet {
    --columns: 6;

    grid-template-columns: repeat(var(--columns), 1fr);
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    margin-bottom: -500;
    gap: 30px;
    padding: 0 39;
  }

  @include design {
    --columns: 12;

    padding: 148px 225px;
    margin-bottom: -400;
  }

  @include desktop {
    --columns: 12;

    padding: 138px 0 0 138;
    margin-bottom: 0;
  }

  @include large-screen {
    padding: 180px 500px 100 707;
  }

  &__start {
    grid-column: 1 / -1;

    @include tablet {
      grid-column: 1 / 4;
    }

    @include desktop {
      grid-column: 1 / 5;
    }

    @include design {
      grid-column: 1 / 4;
    }

    @include large-screen {
      padding: 0;
    }
  }

  &__title {
    font-family: $font-family;
    font-size: 32px;
    line-height: 41.6px;
    margin: 0;
    color: #253757;
    font-weight: 600;
    padding-bottom: 24px;
  }

  &__text {
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    line-height: 21px;
    margin: 0;
    color: #6c788b;
    font-weight: 400;
    padding-bottom: 24px;
  }
}

.first {
  grid-column: 1 / -1;
  padding: 0 32px;

  @include design {
    grid-column: 6 / 9;
  }

  @include desktop {
    width: 100%;
    padding: 0;
    grid-column: 8 / 10;
  }

  &__title {
    font-family: $font-family;
    font-size: 13px;
    line-height: 19.5px;
    margin: 0;
    color: #2060f6;
    font-weight: 600;
    width: 22px;
    height: 20px;
    padding: 2px 12px;
    background-color: rgba(32, 96, 246, 0.1);
    border-radius: 23px;
  }

  &__text {
    font-family: 'Open Sans', sans-serif;
    font-size: 13px;
    line-height: 19.5px;
    margin: 0;
    color: #6c788b;
    font-weight: 600;
    padding: 24px 0 32;
  }

  &__button {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 16;
    border: none;
    font-family: $font-family;
    font-size: 15px;
    line-height: 22.5px;
    margin: 0;
    color: #334563;
    font-weight: 600;
    padding: 0;
    cursor: pointer;
  }
}

